body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  padding: 0;
}



/* Navbar Styles */



.navbar {
  display: flex;
  background-color: black;
  width: 100%;
  z-index: 1;
}

.links{
  padding: 20px;
  padding-left: 900px;
}

.links a{
  text-decoration: none;
  font-size: 20px;
  padding-left: 10px;
  color: rgb(136, 123, 123);
}

.links a:hover{
  color: rgb(250, 249, 249);
}

.add-to-cart {
  display: flex;
  align-items: center;
}

.add-to-cart > img{
  width: 75px;
  height: 75px;
  margin-left: 0px;
}

.navbar h1{
  color: aliceblue;
  padding: 10px;
  padding-left: 20px;
}

span{
  margin-left: -10px;
  color: #fff;
  margin-right: 0px;
  text-decoration: none;
  font-size: 20px;
  background-color: black;
}




/* Contact comonent Styles  */



.container-profile{   
  background-color: rgb(10, 17, 20) ;
  padding: 15px;
  max-width: 350px;
  border-radius: 20px;
  margin: auto;
  opacity: 1;
  box-sizing: border-box;
  padding: 38px;
  color: rgb(226, 227, 228);
  margin-top: 100px;
}

.login h2{
  font-size: 40px;
  text-align:center;
}


input[type=text]{
  width: 100%;
  box-sizing: border-box;
  padding: 12px 5px;
  background:rgba(0,0,0,0.10);
  outline:none;
  border:none;
  border-bottom: 1px dotted #fff;
  color: #fff;
  border-radius: 5px;
  margin: 5px;
  font-weight: bold;
}
input[type=submit]{
  width: 100%;
  box-sizing: border-box;
  padding:10px 0;
  margin-top:30px;
  outline:none;
  border:none;
  background: #60adde;
  opacity:0.7;
  border-radius: 20px ;
  font-size: 20px;
  color: #fff;
}
input[type=submit]:hover{
  background: #003366;
  opacity:0.3;

} 


/* Product Component Styles   */


.dog {
  display: flex;
  flex-direction: column;
  max-height: 500px;
  max-width: 300px;
  margin: 10px;
  background-color: #fff;
  z-index: 1;
  width: 100%;
  padding: 20px;
  justify-content: flex-end;
}

.dog__rating{
  display: flex;
}

.dog__info{
  height: 100px;
  margin-bottom: 0px;
}

.dog__price{
  margin-top: 5px;
}

.dog > img{
  width: 100%;
  max-height: 300px;
  object-fit: contain;
  margin-bottom: 15px;
}

.dog > button{
  background-color: rgba(236, 195, 13, 0.849);
  border: 1px solid;
  border-color: #a88734 #9c7e31 #846a29;
}


/* Home Component Styles */


.container-home {
  max-width: 1500px;
  margin-left: auto;
  margin-right: auto;

}


h2 {
  text-align: center;
  
}

.home__Row {
  display: flex;
  z-index: 1;
  margin-left: auto;
  margin-right: auto;
}

.back-img > img{
  align-content: center;
  width: 100%;
  mask-image: linear-gradient(to bottom, rgba(0,0,0,1), rgba(0,0,0,0));
  z-index: 1;
  height: 700px;
}


/*    Add to cart component Style   */

.checkout{
  color: #fff;
  display: flex;
  padding: 20px;
  height: max-content;
}

.checkout-title{
  margin-right: 10px;
  padding: 10px;
  border-bottom: 1px solid lightgray;
}

.checkout-id{
  width: 100%;
  margin-bottom: 10px;
}